.intro {
	@media (max-width: 767px) {
		margin-top: 100px;
	}
}

.wrapper {
	display: flex;
	padding: 5.6rem 0 0;
}

.inner {
	display: flex;
}

.content {
	width: 50%;

	@media (max-width: 565px) {
		width: 100%;
	}
}

.subtitle {
	font-size: 2.13rem;
	font-weight: 200;
	color: #2b2a2c;
}

.title {
	font-size: 3rem;
	font-weight: 700;
	color: black;

	margin-bottom: 5px;
}

.socialTitle {
	font-size: 18px;
	color: black;
	text-transform: uppercase;
	line-height: 1.2;
	font-weight: 700;

	margin-bottom: 15px;
	padding-top: 45px;
	border-top: 2px solid rgba(80, 67, 141, 0.3);
}

.text {
	margin-bottom: 45px;
	font-size: 1.4rem;
	color: rgba(43, 42, 44, 0.7);

	border-bottom: 2px solid rgba(80, 67, 141, 0.3);
	padding-bottom: 45px;
}

.introButton {
	margin-top: 45px;

	@media (max-width: 565px) {
		display: flex;
		flex-direction: column;
		gap: 20px;
	}
}

.button {
	margin-right: 10px;

	@media (max-width: 565px) {
		margin-right: 0;
	}
}

.photo {
	width: 50%;
	position: relative;
	background: url('./images/symbols_.png') center bottom no-repeat;

	@media (max-width: 565px) {
		display: none;
	}
}

.img {
	height: 100%;
	position: absolute;
	margin: auto;
	left: 0;
	right: 0;
	bottom: 0;
	bottom: 0;
	z-index: 1;
	object-fit: cover;
}

@media (max-width: 900px) {
	.img {
		width: 350px;
	}

	.wrapper {
		padding-bottom: 3rem;
		padding-top: 4.5rem;
	}

	.subtitle {
		font-size: 1.5rem;
	}

	.title {
		font-size: 2rem;
	}
}
